<template><button type="button" class="btn btn-outline-dark" @click="change">按我</button></template>

<script>
import emitter from '@/assets/js/emitter';

export default {
  data() {
    return {
      newText: '最新活動',
    };
  },
  methods: {
    change() {
      emitter.emit('change', this.newText);
    },
  },
};
</script>
